<template>
  <header class="header" @scroll="handlescroll">
    <nav class="nav">
      <ul class="nav-links">
        <li class="linklogo">
          <router-link to="/">
            <span class="span2" id="change">
              <i class="fas fa-cookie-bite"></i>COOKIE</span>
            <span class="span2" style="color:#555">GOAT</span>
          </router-link>
        </li>
       
      </ul>
    </nav>
    <slot></slot>
  </header>
</template>

<script>
export default {
  data() {
    return {};
  },

  methods: {
    handlescroll() {
      let header = document.querySelector(".header");
      let changeText = document.getElementsByTagName("span");

      if (window.scrollY > 100 && !header.className.includes("soild")) {
        header.classList.add("soild");
        changeText.classList.add("white");
      } else if (window.scrollY < 100) {
        header.classList.remove("soild");
        changeText.classList.remove("white");
      }
    }

    // if(this.limitposition > window.scrollY) {
    //   this.scrolled = false;
    // } else {
    //   this.scrolled = true;
    // }
  },

  created() {
    window.addEventListener("scroll", this.handlescroll);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handlescroll);
  }
};
</script>

<style>
.header {
  width: 100%;
  height: 70px;
  background: rgba(255, 255, 255, 0);
  box-sizing: border-box;
  
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease-in-out;


  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;

  position: fixed;
  z-index: 1000;
}

.soild {
  background: rgba(255, 255, 255, 1);
  box-shadow: 1px 2px 15px rgba(100, 100, 100, 0.3);
}

.nav {
  width: 100%;
}

.nav-links {
  width: 100%;
  display: flex;
  justify-content: flex-start;
}

.linklogo {
  list-style: none;

  margin-top: 20px;
}

.span2 {
  color: #eb783e;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 1.5rem;
}
.white {
}
</style>
